
.navbar-sec{
    display: flex;
    align-items: center;
    transition: top .1s;
    background-color: #fff;
    border-bottom: 1px solid #ededed;
    z-index: 501;
}

.navbar-sec-menu{
    -webkit-box-flex: 1;
    flex: 1 0 auto;
    padding: 0 0 0 60px;
    margin: 0 8px 0 -12px;
    text-align: left;
    min-width: 0;
    white-space: nowrap;
    font-size: 0;
}

.navbar-sec-menu_item{
    position: relative;
    display: inline-block;
    color: #424242;
    display: inline-block;
    margin: 8px 4px;
    padding: 6px 8px 4px;
    font-size: 15px;
    line-height: 23px;
    font-weight:400;
    border: none;
    background: none;
    vertical-align: bottom;
    user-select: none;
}

.navbar-sec-menu_item.active,.navbar-sec-menu_item:hover{
    color: #725eff;
}

.navbar-sec-menu_item.active::after{
    margin: 6px -4px 0;
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 100%;
    height:2px;
    background-color: #725eff;
}

.navbar-sec-menu_item.active, .navbar-sec-menu_item--open{
    color: #725eff;
}

.onavbar-sec-content{
    max-height: 49px;
    max-width: 1256px;
    width: 100%;
    display: flex;
    align-items: center;
    -webkit-box-align: start;
    position: relative;
    margin: 0 auto;
    box-sizing: border-box;
    overflow-x: auto;
}
.navbar-sec-add{
    padding-right: 65px;
}

.navbar-sec-add_btn{
    width: 80px;
    padding: 4px 2px;
    font-size: 15px;
    margin: 0;
    border-color: #715eff44;
    box-sizing: border-box;
    font-weight: 700;
    color: #fff;
    background-color: #915eff;
    border-radius: 5px;
    transition: background-color .1s;
    box-shadow: #715effaf 5px 5px 5px;
}

.navbar-sec-add_btn:hover{
    background-color: #725eff;
}