header{
    display: block;
}

.ondo-header{
    box-shadow: 0px 4px 16px rgb(43 52 69 / 10%);
}

.nav-pri{
    transition: top .1s;
    background-color: #fff;
    border-bottom: 1px solid #ededed;
    z-index: 501;
}

.nav-pri-content{
    display: flex;
    height: 80px;
    padding: 10px 60px;
    max-width: 1256px;
    align-items: center;
    position: relative;
    box-sizing: border-box;
    margin: 0 auto;
}

.nav-pri-left{
    position: static;
    margin-right: 15px;
    transform: none;
    flex: 0 0 auto;
    align-self: center;
    font-size: 25px;
    user-select: none;
}

.nav-pri-menu{
    flex: 0 0 auto;
    display: flex;
}

.nav-pri-menu_item{
    position: relative;
    margin: 4px 15px 0;
    padding: 5px;
    font-size: 18px;
    line-height: 25px;
    font-weight:400;
    cursor: pointer;
    user-select: none;
}

.nav-pri-menu_item.active,.nav-pri-menu_item:hover{
    color: #725eff;
}

.nav-pri-menu_item.active, .nav-pri-menu_item--open{
    color: #725eff;
}

.nav-pri-right{
    display: flex;
    position: static;
    flex: 1 0 auto;
    -webkit-box-flex: 1;
    -webkit-box-align: start;
    align-items: flex-start;
    right: 8px;
    justify-content: center;
}

.nav-search{
    flex: 0 1 266px;
    margin: 0px;
    min-width: 0;
    position: relative; 
    display: flex;
}

.nav-search_input{
    position: relative;
    width: 100%;
    box-sizing: border-box;
    padding: 8px 9px 7px 39px;
    font-size: 13px;
    line-height: 20px;
    background-color: #adadad1e;
    border: 1.5px solid #dbdbdb;
    transition: background-color .1s;
    border-radius: 80px;
}

.nav-search_input:hover{
    border-color: #715eff80;
}

.nav-search_icon{
    position: absolute;
    font-size: 18px;
    top: 10px;
    opacity: .5;
    margin-left: 15px;
}


.nav-pri-rightmost{
    flex: 0 0 auto;
    position: static;
    display: flex;
    -webkit-box-align: start;
    align-items: flex-start;
    -webkit-box-pack: end;
    justify-content: flex-end;
    right: 8px;
}

.nav-user-item{
    margin: 20px 10px;
    user-select: none;
    position: relative;
    padding: 6px;
    box-sizing: border-box;
    width: 36px;
    height: 36px;
    background-color: rgba(241, 241, 241, 0.935);
    border: none;
    border-radius: 100%;
    transition: background .1s;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.nav-user-item:hover{
    border: 1.5px solid #dbdbdb;
    border-color: #715eff80;
}

.nav-user-item.active{
    border: 1.5px solid #dbdbdb;
    border-color: #715eff80;
}

.nav-user-like_icon{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    color: rgba(0, 0, 0, 0.736);
    margin-bottom: 0px;
    margin-bottom: -0.5px;
    padding: 10px;
}


.nav-user-id_icon{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    color: rgba(0, 0, 0, 0.736);
    margin-bottom: 1px;
    padding: 10px;
}


.nav-user-id-login{
    height: 570px;
    width: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
}



.nav-user-cart_icon{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 17px;
    color: rgba(0, 0, 0, 0.736);
    margin-bottom: 1px;
    padding: 10px;
}

.nav-user-cart_body{
    width: 400px;
}

.nav-user-cart_header{
    display: flex;
    height: 80px;
    align-items: center;
    padding: 0px 20px;
    border-bottom: 1px solid #6e6e6e33;
}

.nav-user-cart-header_icon{
    font-size: 25px;
    color: rgba(0, 0, 0, 0.736);
}

.nav-user-cart_headname{
    margin-left: 8px;
    font-weight: 600;
    font-size: 16px;
}

.nav-user-cart_main{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: calc(100% - 74px);
    box-sizing:inherit;
    margin-top: 200px;
}

